// holds all the sections
import React from 'react'; 
import Section from './Section';
import './Accordian.css';

class Accordian extends React.Component {
    constructor() {
        super();
        
        this.state = {
            starShip: null,
            loading: true,
            error: null,
            idList: [
                'bga0pzsipb5klgbkfr18.jpg',
                'xyebcp4v7d6vmdblzmel.jpg',
                'xoaiqvn0hnnlg82zrrx1.jpg',
                'krphumhpe13xelxqpcoy.jpg',
                'hmcxnygnbqp3ihkwhktq.jpg',
                'hdoyawmxaj45qqsgfebs.jpg',
                'agtrhekvi72rt4eehdju.jpg',
                'sauh9mvabp4cscn5m3eg.jpg',
                'ftjepi8p1iup7o2wqp83.jpg',
                'aa5buxfclqu5su77ltqx.jpg',
            ]
        };
    }
    
    async componentDidMount() {
        //check if the data from the Star Wars API mounted
        try {
            const data = await fetch('https://swapi.co/api/starships/');
            const ship = await data.json();
            const shipList = ship.results;
            // console.log(shipList)

            this.setState({ 
                starShip: shipList,
                loading: false,
                error: null
            });
            
        }
        catch (err) {
            this.setState({
                loading: false,
                error: true
            });
        }
    }
    
    renderLoading() {
        return(
            <div>
                <h5>Starship data is loading...</h5>
            </div>
        );
    }

    renderError() {
        return(
            <div>
                <p>Something went wrong. Please come back later.</p>
            </div>
        );
    }

    renderSections() {
        const { starShip, loading, error, idList } = this.state;
        // console.log(starShip);
        
        if (error) {
            this.renderError();
        }
        else {
            return (
                <div>
                    {
                        starShip.map((ship,i) => { console.log(starShip[i].starship_class)
                        return(
                            <Section 
                                key={i}
                                id={i}
                                name={starShip[i].name}
                                model={starShip[i].starship_class}
                                cost={starShip[i].cost_in_credits}
                                imageId={idList[i]}
                                //side prop to decide the color of the section when clicked is red or green
                            /> 
                        );
                        })
                    }
                </div>
            );
        }
    }

    render() { 
        const { loading, starShip } = this.state;

        

        return(
            <div className='accordianWrapper'>
                <div className='title'>
                    <h1>Star Wars Starships</h1>
                </div>
                <div className='accordian'>
                    { loading ? this.renderLoading() : this.renderSections() }    
                </div>
            </div>
        );
    }
}

export default Accordian;



