// section to display starships
import React from 'react';
import './Section.css';
import {Image, Video, Transformation, CloundinaryContext } from 'cloudinary-react';


class Section extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            open: false,
            sectionClassName: 'closed',
            headingClassName: 'heading notClicked',
            sectionImage: 'shipImageClosed'
        };

        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        const { open } = this.state;
        if (open) {
            this.setState({
                open: false,
                sectionClassName: 'closed',
                headingClassName: 'heading notClicked',
                sectionImage: 'shipImageClosed'
            });
        }
        else {
            this.setState({
                open: true,
                sectionClassName: 'opened',
                headingClassName: 'heading clicked',
                sectionImage: 'shipImageOpen'
            });
            (this.props.id % 2 === 0) ? this.setState({ headingClassName: 'heading notClicked even' }) : 
            this.setState({ headingClassName: 'heading notClicked odd' });
        }
    }

    sideChecker() {
        (this.props.id % 2 === 0) ? this.setState({ headingClassName: 'heading notClicked even' }) : 
            this.setState({ headingClassName: 'heading notClicked even' });
    }
    
    render() { 
        const { name, model, cost, imageId } = this.props;
        const { sectionClassName,headingClassName, sectionImage } = this.state;
        
        return(
            <div className='accordianBody'> 
                <div className={headingClassName} onClick={this.handleClick}>
                    <h3> {name}</h3>
                </div>
                <div className={sectionClassName}>
                    <p><b>Ship Class:</b> {model}</p>
                    <p><b>Cost:</b> {cost} credits</p>
                
                    <div className={sectionImage}>
                        <Image 
                            cloudName='dxjbbr0ku' 
                            publicId={imageId} 
                            width='300' crop='scale'
                        >    
                            <Transformation quality="auto" fetchFormat="auto" />
                        </Image>
                    </div>
                </div>
            </div>
        );
    }
}

export default Section;


